/* BREAKPOINTS */

$breakpoints : (
        s   : 23.4375em,     // 375px
        m   :  48em,         // 768px
        l   :  64em,         // 1024px
        xl  : 90em,          // 1440px
        xxl : 160em,          // 2560px
);

/* COLORS */

// Primary
$very-dark-magenta  : #502050;      // hsl(300, 43%, 22%)
$soft-pink          : #ee68a4;      // hsl(333, 80%, 67%)

// Neutral
$dark-grayish-magenta   : #937b92;      // hsl(303, 10%, 53%)
$light-grayish-magenta  : #f7f2f7;      // hsl(300, 24%, 96%)
$white                  : #ffffff;      // hsl(0, 0%, 100%)

/* TYPOGRAPHY */

// Fonts
$primary-font : 'Spartan', sans-serif; // @import url('https://fonts.googleapis.com/css2?family=Spartan:wght@400;500;700&display=swap');

// Sizes
$font-s   : .8125rem;     // 13px
$font-sm  : .875rem;      // 14px
$font-m   : .9375rem;     // 15px
$font-l   : 2rem;         // 32px
$font-xl  : 3rem;         // 48px

// Weights
$font-thin    : 400;
$font-regular : 500;
$font-bold    : 700;